<template>
  <div class="addBlog">
    <div class="addMain">
      <div class="row">
        <div class="text">博客标题：</div>
        <div class="iptBox">
          <input type="text" v-model="itemObj.title"/>
        </div>
      </div>

      <div class="row rowAuthor">
        <div class="text">作者：</div>
        <div class="iptBox">
          <input type="text" v-model="itemObj.author"/>
        </div>
      </div>

      <div class="row">
        <div class="text">分类：</div>
        <div class="iptBox">
          <select v-model="itemObj.type">
            <option v-for="item in classList" :key="item.className">{{item.className}}</option>
          </select>
        </div>
      </div>

      <div class="row rowArea">
        <div class="text">博客内容：</div>
        <div class="iptBox">
          <textarea v-model="itemObj.content"></textarea>
        </div>
      </div>

      <div class="row">
        <div class="text"></div>
        <div class="iptBox">
          <button @click="onSubmit">发表博客</button>
        </div>
      </div>
    </div>
    <div v-show="this.itemObj.title||
        this.itemObj.userId||
        this.itemObj.id||
        this.itemObj.body">
      <h1 class="blShowBox">博客预览</h1>
      <BlogItem :itemObj="itemObj"/>
    </div>

  </div>
</template>

<script>
import BlogItem from './BlogItem'
export default {
  data () {
    return {
      classList: [
        { className: 'html5' },
        { className: 'css3' },
        { className: 'Vue' }
      ],
      itemObj: {
        author: '',
        type: '',
        title: '',
        content: '',
        posttime: this.RSFormatDat((new Date()).getTime() / 1000, 0)
      }
    }
  },
  components: {
    BlogItem
  },
  created () {

  },
  methods: {
    onSubmit () {
      if (this.itemObj.title == '' ||
        this.itemObj.type == '' ||
        this.itemObj.content == '' ||
        this.itemObj.author == '') {
        alert('请将内容填写完成后再提交')
        return
      }
      this.posttime = this.RSFormatDat((new Date()).getTime() / 1000, 0)
      this.pushData()
      this.$router.push('/')
    },
    pushData () {
      // this.axios({
      //   url: 'https://vuedemo-b60d5-default-rtdb.firebaseio.com/posts.json',
      //   params: this.itemObj
      // }).then(res => {
      //   console.log(res)
      // })
      this.axios.post('/post', this.itemObj).then(res => {
        alert('提交成功')
      })
    },
    RSFormatDat (dt, it) {
      var _tTime = new Date(parseInt(dt) * 1000)
      if (it == 0) {
        var tHours = _tTime.getHours()
        if (tHours < 10 && tHours >= 0) {
          tHours = '0' + tHours
        }
        var tMinutes = _tTime.getMinutes()
        if (tMinutes < 10 && tMinutes >= 0) {
          tMinutes = '0' + tMinutes
        }
        var tSeconds = _tTime.getSeconds()
        if (tSeconds < 10 && tSeconds >= 0) {
          tSeconds = '0' + tSeconds
        }
        return _tTime.getFullYear() + '年' + (parseInt(_tTime.getMonth(), 10) + 1) + '月' + _tTime.getDate() + '日' + tHours + ':' + tMinutes + ':' + tSeconds
      } else {
        return _tTime.getFullYear() + '年' + (parseInt(_tTime.getMonth(), 10) + 1) + '月' + _tTime.getDate() + '日'
      }
    }
  }

}
</script>

<style lang="less" scoped>
input,
textarea,
select {
  outline: none;
}
.addBlog {
  padding: 50px 30px;
  min-height: 830px;
}
.addMain {
  padding: 0 80px;
  font-size: 18px;
  color: #333;
  .row {
    padding: 10px 0;
    display: flex;
    align-items: center;
    .text {
      width: 20%;
      text-align: right;
      padding-right: 10px;
    }
    .iptBox {
      width: 80%;
      input,
      textarea {
        width: 100%;
        padding: 10px;
        height: 38px;
        border: 1px solid #ccc;
        border-radius: 3px;
        transition: 0.3s;
        font-size: 16px;
        color: #666;
        &:focus {
          border-color: rgb(6, 104, 161);
          box-shadow: 0 0 10px rgba(65, 184, 131, 0.3);
        }
      }
      select {
        width: 50%;
        height: 38px;
        border: 1px solid #ccc;
        border-radius: 3px;
        padding: 0 10px;
        font-size: 16px;
        color: #333;
      }
      textarea {
        height: 200px;
      }
      button {
        width: 50%;
        height: 38px;
        background: rgb(6, 104, 161);
        color: #fff;
        border: none;
        border-radius: 3px;
        cursor: pointer;
        transition: 0.3s;
        &:hover {
          background: #0648d6;
        }
      }
    }
  }
  .rowArea {
    align-items: flex-start;
  }
}

.blShowBox {
  font-size: 22px;
  color: #555;
  padding: 50px 0 30px;
  text-align: center;
  border-top: 1px solid #e4e4e4;
  margin-top: 30px;
}
</style>
